<template>
  <div id="wrap">
    <div id="top" @click="back()">
      <img src="../../assets/img/back.png">
      <p></p>
    </div>
    <div id="tex1">
        20161010 313034 
    </div>
    <div id="tex2">
        <p class="tit">Q1: </p>
        <p></p>
        <p> 313034</p>
        <p></p>
    </div>
    <div id="tex3">
         <p class="tit">Q2: </p>
         <p>20</p>
    </div>
    <div id="tex4">
         <p class="tit">Q3: </p>
         <p></p>
         <div id="content">
             <p>31</p>
         <p>3130</p>
         </div>
    </div>
    <div id="tex5">
         <p class="tit">Q4: </p>
         <p>App>></p>
    </div>
    <div id="tex6">
         <p class="tit">Q5: </p>
         <p></p>
         <p></p>
    </div>
  </div>
</template>
<script>
export default {
  name: "discount",
  data() {
    return {};
  },
  methods: {
    back() {
      this.$router.back();
    }
  }
};
</script>
<style scoped>
#wrap {
  width: 3.75rem;
  position: relative;
}
#top {
  width: 100%;
  height: 0.55rem;
  background-color: rgb(49, 144, 232);
  position: fixed;
  z-index: 2;
  left: 0;
  top: 0;
}
#top img {
  height: 0.25rem;
  position: absolute;
  left: 5%;
  top: 50%;
  transform: translate(-50%, -50%);
}
#top p {
  font-size: 0.2rem;
  color: white;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
#tex1{
    margin-top: 0.65rem;
    /* border: 1px solid white; */
    width: 3rem;
    font-size: 0.15rem;
    margin-left: 0.375rem;
}
.tit{
    font-size: 0.18rem;
    font-weight: bold;
}
#tex2,#tex3,#tex4,#tex5,#tex6{
    width: 3.55rem;
    /* border: 1px solid white; */
    margin-left: 0.1rem;
    font-size: 0.15rem;
    margin-top: 0.12rem;
}
#tex2 p:nth-child(2),#tex2 p:nth-child(3),#tex2 p:nth-child(4){
    margin-top: 0.05rem;
}
#content{
    /* border: 1px solid red; */
     width: 3.1rem;
    /* border: 1px solid white; */
    margin-left: 0.54rem;
    font-size: 0.15rem;
    margin-top: 0.12rem;
}
</style>



