<template>
  <div id="wrap">
    <div id="top" @click="back()">
      <img src="../../assets/img/back.png">
      <p></p>
    </div>
    <div id="tex">
        <p class="tex1">Q1:?</p>
        <div class="tex2">
            <p>,</p>
        </div>

        <p class="tex1">Q2:?</p>
        <div class="tex2">
            <p>[</p>
            <p></p>
        </div>

        <p class="tex1">Q3:</p>
        <div class="tex2">
            <p></p>
            <p></p>
            <p></p>
        </div>

    </div>
  </div>
</template>
<script>
export default {
    name:"shopcouponexplain",
    data() {
        return {
            
        }
    },
    methods: {
        back(){
            this.$router.back();
        }
    },
};
</script>
<style scoped>
#wrap {
  width: 3.75rem;
  position: relative;
}
#top {
  width: 100%;
  height: 0.55rem;
  background-color: rgb(49, 144, 232);
  position: fixed;
  z-index: 2;
  left: 0;
  top: 0;
}
#top img {
  height: 0.25rem;
  position: absolute;
  left: 5%;
  top: 50%;
  transform: translate(-50%, -50%);
}
#top p {
  font-size: 0.2rem;
  color: white;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
#tex{
  margin-top: 0.55rem;
  width: 3.35rem;
  /* border: 1px solid red; */
  margin-left: 0.2rem;
}
.tex1{
  font-size: 0.18rem;
  font-weight: bold;
  margin-top: 0.1rem;
}
.tex2{
  font-size: 0.15rem;
  margin-top: 0.1rem;
}
</style>



